.growtype-map-container {
  width: 100%;
  min-height: 500px;
}

.growtype-map-container-wrapper {
  position: relative;
}
.growtype-map-container-wrapper.has-cover {
  cursor: pointer;
}
.growtype-map-container-wrapper .spinner-border {
  height: 20px;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 44%;
  width: 20px;
  z-index: 1;
  display: none;
}
.growtype-map-container-wrapper.is-loading .spinner-border {
  display: block;
}
.growtype-map-container-wrapper.is-loading .growtype-map-container {
  pointer-events: none;
  opacity: 0.8;
}

.growtype-map-taxonomy {
  width: 100%;
}
.growtype-map-taxonomy .growtype-map-taxonomy-nav {
  display: flex;
  gap: 15px;
  padding-top: 20px;
  padding-bottom: 15px;
}
.growtype-map-taxonomy .growtype-map-taxonomy-item .btn-img {
  margin-right: 2px;
  max-width: 15px;
  position: relative;
  top: -1px;
}
.growtype-map-taxonomy .growtype-map-taxonomy-item .btn-label {
  bottom: -1px;
  cursor: pointer;
  margin: 0;
  position: relative;
  font-size: 14px;
  text-align: left;
}
.growtype-map-taxonomy .growtype-map-taxonomy-item svg {
  max-width: 100%;
  width: 100%;
  height: 100%;
}
.growtype-map-taxonomy .growtype-map-taxonomy-item .btn {
  border: 1px solid #e5e5e5;
  cursor: pointer;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  align-items: center;
  gap: 5px;
}
.growtype-map-taxonomy .growtype-map-taxonomy-item .btn.is-active {
  background: #191919;
  color: white;
}
.growtype-map-taxonomy .growtype-map-taxonomy-item .btn.is-active svg path {
  fill: white;
}